import Vue from 'vue'
import App from './App.vue'

import VueRouter from 'vue-router'
//引入路由器
import router from './router'
import MintUI from "mint-ui"
import 'mint-ui/lib/style.css';
//关闭Vue的生产提示
Vue.config.productionTip = false
//应用插件
Vue.use(VueRouter)
Vue.use(MintUI)
new Vue({

  render: h => h(App),
  router:router
}).$mount('#app')
